export class Participant {
    index: number;
    name: string;
    description: string;
    socials: Socials;

    constructor(
        index: number,
        name: string,
        description: string,
        socials: Socials
    ) {
        this.index = index;
        this.name = name;
        this.description = description;
        this.socials = socials;
    }
}

export class Socials {
    facebook?: string;
    instagram?: string;
    twitter?: string;
    vk?: string;
    telegram?: string;

    constructor({
        facebook,
        instagram,
        twitter,
        vk,
        telegram,
    }: {
        facebook?: string;
        instagram?: string;
        twitter?: string;
        vk?: string;
        telegram?: string;
    }) {
        this.facebook = facebook;
        this.instagram = instagram;
        this.twitter = twitter;
        this.vk = vk;
        this.telegram = telegram;
    }
}
